import React, { useEffect, useState } from 'react';
import './Scss/Infografics.scss';
import Slider from 'react-slick';
import CountUp from 'react-countup';
import { useTranslation } from 'react-i18next';
import Img from '../../assets/images/infografic.png';

const Infografics = ({ dark, english, russian, uzbek, mandarin }) => {

    const [number1, setNumber1] = useState(0);
    const [number2, setNumber2] = useState(0);
    const [number3, setNumber3] = useState(0);
    const [number4, setNumber4] = useState(0);

    const observer = new IntersectionObserver(
        entries => {
            entries.forEach(entry => {
                window.addEventListener("scroll", () => {
                    if (window.scrollY - entry.target.offsetTop > -window.innerHeight) {
                        setNumber1(1000)
                        setNumber2(300)
                        setNumber3(600)
                        setNumber4(150)
                    } else if (window.scrollY - entry.target.offsetTop < 0) {
                        setNumber1(0)
                        setNumber2(0)
                        setNumber3(0)
                        setNumber4(0)
                    }
                })
            })
        }
    );

    useEffect(() => {
        const infografics = document.getElementById("infografics");
        observer.observe(infografics);
    }, []);

    let dataNumbers = [
        { id: 0, number: number1, text_uz: "Yillik loyihalar soni", text_ru: "   ", text_en: "Number of projects per year", text_mn: "", description_uz: "Biz yiliga keltirilgan raqamlardagi loyihalarni bajaramiz. Bularning ichida Temir yol orqali yetkazib berishlar, Havo yoli orqali yetkazib berishlar, Quruqlik orqali yetkazib berishlarni amalga oshiramiz. Siz ham bizning mijozimiz bolishingiz uchun bizga qongiroq qiling va sevimli mijozlarimizdan biriga aylaning", description_ru: "    ,   .        ,       .         ,    ", description_en: "We carry out projects in the numbers indicated per year. Among them, we carry out deliveries by railway, deliveries by air, and deliveries by land. Call us and become one of our favorite customers to become our customer", description_mn: "  " },
        { id: 1, number: number2, text_uz: "Ortiq davlatlarga chiqish", text_ru: "    ", text_en: "Access to more countries", text_mn: "", description_uz: "Biz qariyb barcha dunyo mamlakatlari bilan hamkorlik asosida ishlaymiz va sizning yukingizni istalgan joydan olib kelamiz yoki yetkazib beramiz", description_ru: "                   .", description_en: "We work in cooperation with almost all countries of the world and we can pick up or deliver your cargo from anywhere", description_mn: "" },
        { id: 2, number: number3, text_uz: "Minnatdor mijozlar", text_ru: " ", text_en: "Grateful customers", text_mn: "", description_uz: "Bizning xizmatimizdan foydalangan mijozlarimiz soni bundanda ortiq. Siz ham ularning biriga aylaning", description_ru: " ,   ,  .    ", description_en: "The number of clients who have used our service is more than that. Become one of them", description_mn: " " },
        { id: 3, number: number4, text_uz: "Xodimlarimiz soni", text_ru: "  ", text_en: "Number of our employees", text_mn: "", description_uz: "Bizda oz sohasaning mutaxassislaridan tashkil topgan jamoa bolib sizning yukingizni tez va xavfsiz yetkazib berishga kafolat beramiz", description_ru: "      ,         ", description_en: "We have a team of experts in our field, and we guarantee fast and safe delivery of your cargo", description_mn: "" }
    ];

    const [activeId, setActiveId] = useState(0);

    const settings = {
        dots: true,
        infinite: true,
        autoplay: true,
        slidesToShow: 1,
        speed: 2000,
        pauseOnHover: false,
        slidesToScroll: 1,
        autoplaySpeed: 5000,
        cssEase: "linear",
        beforeChange: (current, next) => setActiveId(next),
    };

    // i18next

    const { t } = useTranslation();

    return (
        <div className={`Infografics main ${!dark && "light-infografics"}`} id='infografics'>
            <div className="title">{t("infographic")}</div>
            <div className="wrapper">
                <img src={Img} alt="truck" className='side-truck' />
                <div className="rectangle"></div>
                <div className="numbers">
                    {dataNumbers.map((data) => (
                        <div key={data.id} className="number col-3">
                            <div className="body" onClick={() => setActiveId(data.id)}>
                                <h1 className={`count ${activeId == data.id && "active"}`}>
                                    <CountUp start={0} end={data.number} duration={4} />
                                </h1>
                                <p className={`text ${data.id != activeId && "d-none"}`}>{uzbek && data.text_uz}{russian && data.text_ru}{english && data.text_en}{mandarin && data.text_mn}</p>
                            </div>
                        </div>
                    ))}
                </div>
                <Slider {...settings} className='carousel'>
                    {dataNumbers.map((data) => (
                        <p key={data.id} className="desc">{uzbek && data.description_uz}{russian && data.description_ru}{english && data.description_en}{mandarin && data.description_mn}</p>
                    ))}
                </Slider>
            </div>
        </div>
    );
};

export default Infografics;